.footer {
  padding-bottom: 60px;
  background-color: var(--bg-white);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  box-shadow: 0px -4px 24px rgba(105, 105, 105, 0.1);
  background-image: url(../images/fruits/footer/footer-mobile.png);
  @include retina-bgi {
    background-image: url(../images/fruits/footer/footer-mobile@2x.png);
  }
}

.footer__nav {
  display: flex;
}
.nav-link-logo {
  padding-top: 60px;
  padding-left: 20px;
}
.nav-list {
  padding-top: 64px;
  margin-left: 84px;
}
.nav-item:not(:last-child) {
  margin-bottom: 20px;
}
.nav-link {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;

  color: var(--black);
}
.nav-link:hover,
.nav-link:focus {
  color: var(--btn-green);
}
