.footer__subscribe {
  display: flex;
  flex-direction: column;
  padding-top: 40px;
}
.subscribe-text {
  font-weight: 500;
  font-size: 14px;
  line-height: 1.5;

  color: var(--title-black);
}
.subscribe-input {
  padding-left: 12px;
  margin-top: 12px;
  margin-bottom: 20px;
  width: 100%;
  height: 42px;
  background-color: var(--white);

  // border-color: 0.854167px solid var(--yellow);
  border-radius: 30px;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.5;
  outline: none;
  // color: var(--title-grey);
}
.subscribe-input::placeholder {
  color: var(--title-grey);
}
.subscribe-input:not(:placeholder-shown):required:valid {
  border: 0.854167px solid #7eb101;
}

.subscripte-input:not(:placeholder-shown):required:invalid {
  border: 0.854167px solid #ff705d;
}
.subscribe-input {
  border: 0.854167px solid var(--yellow);
}
.subscribe-button {
  margin-bottom: 20px;
  width: 103px;
  height: 42px;
  margin-left: auto;
  background: var(--white);
  border: 0.854167px solid var(--title-green);

  color: var(--title-black);
}
.subscribe-button:hover,
.subscribe-button:focus {
  border: 2px solid #ffe450;
}
.footer__text {
  font-weight: 400;
  font-size: 10px;
  line-height: 1.2;

  color: var(--title-black);
}
.privacy-link {
  font-weight: 400;
  font-size: 10px;
  line-height: 1.2;
  text-decoration-line: underline;
  color: var(--title-green);
}
.footer__privacy {
  margin-top: 60px;
  margin-bottom: 40px;
  display: flex;
  justify-content: space-between;
}
.footer-text {
  font-weight: 400;
  font-size: 10px;
  line-height: 1.2;
  color: var(--title-black);
  text-align: center;
}
